body {
  width: 100%;
}

.container {
  width: 500px;
  margin: 20px;
  border: 2px solid #C6CBCF;
  border-radius: 5px;
  text-align: auto;
}

h1 {
  background-color: #428BCA;
  color: white;
  margin-top: 0;
  padding: 20px;
}

.formBody {
  position: relative;
  width: 350px;
  // margin: auto;

}

.fieldBox {
  position: relative;
}

input {
  width: 400px;
  margin: 10px;
  padding: 10px;
  padding-left: 30px;
  background-color: white;
  border: 2px solid #D3D7D9;
  border-radius: 5px;
}

.textarea {
  width: 400px;
  border-radius: 5px;
  padding: 10px;

}

i {
  position: absolute;
  top: 22px;
  left: 22px;
}
.footer {
  background-color: #C9DEF0;
  padding: 20px;
  margin-top: 30px;

}
button {
  width: 150px;
  height: 50px;
  color: white;
  background-color: #428BCA;
  border-radius: 5px;
  border: 1px solid #3583C6;


}
